* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100vh;
}
.body--scrolloff {
  overflow: hidden;
}
.header {
  width: 100%;
  padding: 10px 20px;
  background-color: #DCDCDC;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  width: 140px;
  height: 80px;
}
.header__enter-btn {
  padding: 15px 10px;
  border: none;
  border-radius: 12px;
  color: #fff;
  background-color: #6600FF;
  text-transform: uppercase;
  font-weight: bold;
  font-family: 'Roboto', sans-serif;
  cursor: pointer;
  box-shadow: 0 3px 10px rgb(0 0 0);
  transition: 0.4s all;
}
.header__enter-btn:hover,
.header__enter-btn:focus {
  color: #DCDCDC;
  box-shadow: 0 5px 12px rgb(0 0 0);
}
.header__enter-btn:active {
  opacity: 0.8;
}
.main {
  width: 100%;
  height: 100%;
  display: flex;
}
.main__content {
  width: 100%;
  height: 100%;
}
.menu {
  padding: 40px 0;
  width: 280px;
  background-color: #8c92ac;
}
.menu__list {
  list-style: none;
  display: grid;
  grid-template-rows: repeat(3, min-content);
  grid-row-gap: 20px;
}
.menu__item {
  padding: 5px 10px;
  height: min-content;
  border-bottom: 2px solid #696969;
  border-top: 2px solid #696969;
}
.menu__button {
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
  display: grid;
  grid-template-columns: 1fr 40px;
  grid-column-gap: 5px;
  cursor: pointer;
}
.menu__button-icon {
  transform: rotate(-90deg);
  width: 30px;
  height: 30px;
}
.menu__button-icon--open {
  transform: rotate(90deg);
}
.menu__button-text {
  color: #000;
  font-size: 18px;
  font-weight: bold;
  text-align: left;
}
.menu__item-content {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: none;
}
.menu__item-content--open {
  display: block;
}
.menu__link {
  color: #000;
  text-decoration: none;
  cursor: pointer;
  text-decoration: underline;
  text-transform: uppercase;
}
